import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
// import Icon from "@material-ui/core/Icon";
import Container from "@material-ui/core/Container";
// @material-ui/icons
import Notification from '../Notification'
//import Email from "@material-ui/icons/Email";
// import People from "@material-ui/icons/People";
import People from "@material-ui/icons/PersonRounded";
import LockIcon from '@material-ui/icons/Lock';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import IconButton from '@material-ui/core/IconButton';
// core components
import Header from "../Header/Header.js";
import GridContainer from "../Grid/GridContainer.js";
import GridItem from "../Grid/GridItem.js";
import Button from "../CustomButtons/Button";
import Card from "../Card/Card.js";
import CardBody from "../Card/CardBody.js";
import CardHeader from "../Card/CardHeader.js";
import CardFooter from "../Card/CardFooter.js";
import CustomInput from "../CustomInput/CustomInput.js";
import styles from "../../assets/jss/material-kit-react/views/loginPage.js";

//import image from "../../assets/img/bg4.jpg";
// import bg1 from "../../assets/img/bg1.jpg";
// import bg2 from "../../assets/img/bg2.jpg";
// import bg3 from "../../assets/img/bg3.jpg";
// import bg4 from "../../assets/img/bg4.jpg";

import * as api from '../../api';
import { host} from '../../conf';
import Errors from '../Errors';
import Video from '../Video/Container - 23759.mp4';


const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [user, updateUser] = React.useState({ username: "", password: "" });
  const [errors] = React.useState([]);
  //const [errors, updateErrors] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [showPassword, setShowPassword] = React.useState(false);
  const [password, setPassword] = React.useState(false);
  //const handleClickShowPassword = (e) => {e.preventDefault();setShowPassword({showPassword : showPassword}) };
  //const handleMouseDownPassword = (e) => {e.preventDefault();setShowPassword({showPassword : showPassword}) };
  //const pictureArray = [bg1, bg2, bg3, bg4];
  //const randomIndex = Math.floor(Math.random() * pictureArray.length);
  //const selectedPicture = pictureArray[randomIndex];

  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  // const modal_style = {display:'flex',alignItems:'center',justifyContent:'center'};
  const { ...rest } = props;
  const NoData = ()=>{
    setNotify({isOpen: true, message: 'No deje campos en blanco', errors: [], type: 'error'});
  };
  const login = () => {
    api.postData(host + '/rest-auth/login/', user)
    .then(data => {
        if(data.status === 401){
          sessionStorage.removeItem("session-token");
          login();
        }
        if(data.status !== 200){
            let messages = [];
            Object.keys(data.data).map(key => messages.push(data.data[key]));
            setNotify({isOpen: true, message: messages, errors: [], type: 'error'});
            //updateErrors(messages);
            // Swal.fire({
            //   position: 'top',
            //   //icon: 'error',
            //   text: messages,
            //   showConfirmButton: false,
            //   timer: 2500
            // })
        } else {
            sessionStorage.setItem('session-token', data.data.key);
            //props.updateLogin(true);
            api.getData(host +'/api/usuariosuser/rolactual/')
            .then(data => {
                    sessionStorage.setItem('session-user', user.username);
                    sessionStorage.setItem('session-rol', data.data);

                    localStorage.setItem('session-token', true);
                    localStorage.setItem('session-rol', true);
                    localStorage.setItem('session-user', true);
                    
                    props.setRolCurrent(data.data)
                    props.setUserCurrent(user.username)
                    
                    props.updateLogin(true);                    
                    props.updateUser(true);
                    props.updateRol(true);
             }).catch( e=>
              { 
                setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
              });                 

        }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });

      
      
  };
  const handleTogglePassword = (any) => setShowPassword(showPassword => !showPassword);
  return (
    <div>
       <video autoPlay loop muted
          style={{
            position:"absolute",
            width:"100%",
            left:"50%", 
            top:"50%",
            height:"100%",
            objectFit:"cover",
            transform:"translate(-50%, -50%)",
            zIndex:"-1"
          }}
          >
          <source src={Video} type="video/mp4"/>
      </video>
      <Header
        absolute
        color="transparent"
        brand="Get it Done"
        {...rest}
      />
      <div className={classes.pageHeader} 
        style={{
          //backgroundImage:  `url(${image})`,
          backgroundSize: "cover",
          backgroundPosition: "top center",
          height: "100vh",
          width: "100vw",        
          backgroundRepeat: "no-repeat",
          overflow:"hidden",          
        }}
      >
        <Container >
        <div className={classes.container}>                  
          <GridContainer justify="center">
          {/* <container > */}
            <GridItem xs={10} sm={6} md={4} >  {/* xs={8} lg={4} sm={8} md={4}> */}
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form}>
                  <CardHeader color="primary" className={classes.cardHeader}>
                    <h3>Cliente TATC</h3>
                    <div className={classes.socialLine}>   
                    </div>
                  </CardHeader>                 
                  <CardBody>
                    <Errors errors={errors} ></Errors>
                    <CustomInput
                      labelText="Usuario"
                      id="username"
                      value={user}
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        onChange: (event) => {
                          //const value = event.target.value;
                          //setUserValue(value)
                          updateUser({...user, username: event.target.value})
                        }, 
                        type: "text",                        
                        startAdornment: (
                          <InputAdornment position="start">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}                      
                    />                    
                    <CustomInput
                      labelText="Contraseña"
                      id="pass"
                      value = {password}
                      type= "password"                        
                      formControlProps={{
                        fullWidth: true,
                      }}                      
                      inputProps={{
                        type: showPassword?"text":"password", 
                        onChange: (event) => {
                          const value = event.target.value;
                          setPassword(value);
                          updateUser({...user, password: event.target.value});
                        },                       
                        startAdornment: (
                          <InputAdornment position="start">
                           <LockIcon className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton
                              aria-label="toggle password visibility"
                              onClick={handleTogglePassword}
                              //onMouseDown={#}
                            >
                              {showPassword ? <VisibilityIcon className={classes.inputIconsColor} /> : <VisibilityOffIcon className={classes.inputIconsColor}  />}
                            </IconButton>
                          </InputAdornment>
                        ),                       
                        autoComplete: "off",                       
                      }}                      
                    />
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                  {user.username && user.password ?(
                      <Button color="primary" onClick={login} round fullWidth>
                        Iniciar Sesión
                      </Button>
                    ):(
                      <Button color="primary" onClick={NoData} round fullWidth>
                        Iniciar Sesión
                      </Button>
                    )}
                  </CardFooter>
                </form>
              </Card>
            </GridItem>
            {/* </container> */}
          </GridContainer>
          
          <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
          </Notification>
        </div>
        </Container>
      </div>
    </div>
  );
}
